* {
  box-sizing: border-box;
}

html, body {
  margin: 0;
  padding: 0;
}

html {
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 16px;
  line-height: 1.5;
}

@media (min-width: 38em) {
  html {
    font-size: 20px;
  }
}

body {
  color: #515151;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  background-color: #fff;
}

a {
  color: #268bd2;
  text-decoration: none;
}

a:hover, a:focus {
  text-decoration: underline;
}

h1, h2, h3, h4, h5, h6 {
  color: #313131;
  text-rendering: optimizelegibility;
  margin-bottom: .5rem;
  font-weight: bold;
  line-height: 1.25;
}

h1 {
  font-size: 2rem;
}

h2 {
  margin-top: 1rem;
  font-size: 1.5rem;
}

h3 {
  margin-top: 1.5rem;
  font-size: 1.25rem;
}

h4, h5, h6 {
  margin-top: 1rem;
  font-size: 1rem;
}

p {
  margin-top: 0;
  margin-bottom: 1rem;
}

strong {
  color: #303030;
}

ul, ol, dl {
  margin-top: 0;
  margin-bottom: 1rem;
}

dt {
  font-weight: bold;
}

dd {
  margin-bottom: .5rem;
}

hr {
  border: 0;
  border-top: 1px solid #eee;
  border-bottom: 1px solid #fff;
  margin: 1.5rem 0;
  position: relative;
}

abbr {
  color: #555;
  text-transform: uppercase;
  font-size: 85%;
  font-weight: bold;
}

abbr[title] {
  cursor: help;
  border-bottom: 1px dotted #e5e5e5;
}

code, pre {
  font-family: Menlo, Monaco, Courier New, monospace;
}

code {
  color: #bf616a;
  background-color: #f9f9f9;
  border-radius: 3px;
  padding: .25em .5em;
  font-size: 85%;
}

pre {
  background-color: #f9f9f9;
  margin-top: 0;
  margin-bottom: 1rem;
  padding: 1rem;
  font-size: .8rem;
  line-height: 1.4;
  display: block;
  overflow-x: scroll;
}

pre code {
  color: inherit;
  background-color: #0000;
  padding: 0;
  font-size: 100%;
}

.highlight {
  border-radius: 4px;
  margin-bottom: 1rem;
}

.highlight pre {
  margin-bottom: 0;
}

blockquote {
  color: #7a7a7a;
  border-left: .25rem solid #e5e5e5;
  margin: .8rem 0;
  padding: .5rem 1rem;
}

blockquote p:last-child {
  margin-bottom: 0;
}

@media (min-width: 30em) {
  blockquote {
    padding-left: 1.25rem;
    padding-right: 5rem;
  }
}

img {
  max-width: 100%;
  border-radius: 5px;
  margin: 0 0 1rem;
  display: block;
}

table {
  width: 100%;
  border-collapse: collapse;
  border: 1px solid #e5e5e5;
  margin-bottom: 1rem;
}

td, th {
  border: 1px solid #e5e5e5;
  padding: .25rem .5rem;
}

tbody tr:nth-child(2n+1) td, tbody tr:nth-child(2n+1) th {
  background-color: #f9f9f9;
}

.lead {
  font-size: 1.25rem;
  font-weight: 300;
}

.message {
  color: #717171;
  background-color: #f9f9f9;
  margin-bottom: 1rem;
  padding: 1rem;
}

.container {
  max-width: 38rem;
  margin-left: auto;
  margin-right: auto;
  padding-left: 1rem;
  padding-right: 1rem;
}

.masthead {
  margin-bottom: 3rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.masthead-title {
  color: #505050;
  margin-top: 0;
  margin-bottom: 0;
}

.masthead-title a {
  color: #505050;
}

.masthead-title small {
  color: silver;
  letter-spacing: 0;
  font-size: 75%;
  font-weight: 400;
}

.page, .post {
  margin-bottom: 4em;
}

.page-title, .post-title, .post-title a {
  color: #303030;
}

.page-title, .post-title {
  margin-top: 0;
}

.post-date {
  color: #9a9a9a;
  margin-top: -.5rem;
  margin-bottom: 1rem;
  display: block;
}

.related {
  border-top: 1px solid #eee;
  padding-top: 2rem;
  padding-bottom: 2rem;
}

.related-posts {
  padding-left: 0;
  list-style: none;
}

.related-posts h3 {
  margin-top: 0;
}

.related-posts li small {
  color: #999;
  font-size: 75%;
}

.related-posts li a:hover {
  color: #268bd2;
  text-decoration: none;
}

.related-posts li a:hover small {
  color: inherit;
}

.footnote-definition:first-of-type {
  border-top: 1px solid;
  padding-top: 1rem;
}

.footnote-definition {
  margin-top: 1rem;
}

.footnote-definition sup {
  float: left;
  margin-left: -1rem;
}

.pagination {
  color: #ccc;
  text-align: center;
  margin-left: -1rem;
  margin-right: -1rem;
  font-family: PT Sans, Helvetica, Arial, sans-serif;
  overflow: hidden;
}

.pagination-item {
  border: 1px solid #eee;
  padding: 1rem;
  display: block;
}

.pagination-item:first-child {
  margin-bottom: -1px;
}

a.pagination-item:hover {
  background-color: #f5f5f5;
}

@media (min-width: 30em) {
  .pagination {
    margin: 3rem 0;
  }

  .pagination-item {
    float: left;
    width: 50%;
  }

  .pagination-item:first-child {
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
    margin-bottom: 0;
  }

  .pagination-item:last-child {
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    margin-left: -1px;
  }
}

